body {
  margin: 0;
  padding: 0;
  
  font-family: sans-serif;
}

.steps {
  -webkit-align-self: center;
          align-self: center;
  margin-top: 10px;
}

.step {
  -webkit-align-self: center;
          align-self: center;
 }

.substeps {
  -webkit-align-self: center;
          align-self: center;
   
}

.error {
  -webkit-align-self: center;
          align-self: center;
  margin-top: 20px;
   
}

.toggleSubsteps {
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  color: #072f5c;
  
}

.latex {
  padding: 7px 0;
 
}

.changeDescription {
  color: #2196f3;
  
  padding: 5px 0;
}

@media only screen and (max-width: 480px) {
 

  .toggleSubsteps {
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    color: #072f5c;
    -webkit-align-self: center;
            align-self: center;
    
  }

  .latex {
    padding: 7px 0;
    margin: auto;
     -webkit-align-self: center;
             align-self: center;

  }
  .changeDescription {
    color: #2196f3;
    margin: auto;
    -webkit-align-self: center;
            align-self: center;

 
    padding: 10px 0;
  }
}
  
